import React from "react";
import "../First/First.css";
import ProgressBar from "react-bootstrap/ProgressBar";
const backBtn = require("../../Assets/botao_voltar_proximo.svg");
const pauseBtn = require("../../Assets/botao_pause.svg");

export default function PreQuestion({ changeScreen, answer }) {
  const handleYes = () => {
    answer(true);
    return changeScreen(1);
  };

  const handleNo = () => {
    answer(false);
    return changeScreen(1);
  };

  return (
    <div className="container">
      <div className="game-content">
        <div className="question-container">
          <p className="question-title">
            Voc√™ j√° produz conte√∫do audiovisual em alguma plataforma?
          </p>
        </div>
        <div className="button-container">
          <button
            className="button yn-button"
            type="button"
            onClick={() => handleYes()}
          >
            <p style={{ marginTop: 8 }}>Sim</p>
          </button>
          <button
            className="button yn-button"
            type="button"
            onClick={() => handleNo()}
          >
            <p style={{ marginTop: 8 }}>N√£o</p>
          </button>
        </div>
        <div className="progress-bar">
          <ProgressBar
            animated
            variant="danger"
            now={5}
            style={{ marginBottom: 20 }}
          />
        </div>
        <div className="player-button-container">
          <img
            className="player-button"
            alt="Imagem de voltar de um player"
            src={backBtn}
          />
          <img
            className="player-button"
            alt="Bot√£o de pause de um player"
            src={pauseBtn}
          />
          <img
            className="player-button rotate"
            alt="Imagem de ir para frente de um player"
            src={backBtn}
          />
        </div>
      </div>
    </div>
  );
}
